@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
:root {
    --hex-green: #57c754;
    --hex-red: #f27a41;
    --hex-blue: #0fadd4;
    --hex-yellow: #e0e016;
    --hex-white: #77ccd1;
    --hex-purple: #a77ecf;
    --hex-brown: #dba560;
    --hex-pink: #e37197;
    --hex-gray: #829299;
    --hex-black: #0d3140;
}

.pokemon-green {background: #57c754;background: var(--hex-green);}
.pokemon-red {background: #f27a41;background: var(--hex-red);}
.pokemon-blue {background: #0fadd4;background: var(--hex-blue);}
.pokemon-yellow {background: #e0e016;background: var(--hex-yellow);}
.pokemon-white {background: #77ccd1;background: var(--hex-white);}
.pokemon-purple {background: #a77ecf;background: var(--hex-purple);}
.pokemon-brown {background: #dba560;background: var(--hex-brown);}
.pokemon-pink {background: #e37197;background: var(--hex-pink);}
.pokemon-gray {background: #829299;background: var(--hex-gray);}
.pokemon-black {background: #0d3140;background: var(--hex-black);}

.color-green {color: #57c754;color: var(--hex-green);}
.color-red {color: #f27a41;color: var(--hex-red);}
.color-blue {color: #0fadd4;color: var(--hex-blue);}
.color-yellow {color: #e0e016;color: var(--hex-yellow);}
.color-white {color: #77ccd1;color: var(--hex-white);}
.color-purple {color: #a77ecf;color: var(--hex-purple);}
.color-brown {color: #dba560;color: var(--hex-brown);}
.color-pink {color: #e37197;color: var(--hex-pink);}
